import { useHistory, useParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import styled from "styled-components";
import "./Details.scss";

const BOX = styled.div`
  padding: 20px;
`;
const TITLE = styled.h4`
  font-size: 25px;
  color: ${(props) => props.color};
`;

function Details(props) {
  const [myAlert, setmyAlert] = useState(true);

  useEffect(() => {
    //axios.get()
    let timer = setTimeout(() => {
      setmyAlert(false);
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, []);

  const { id } = useParams();
  const history = useHistory();
  const selectedItem = props.products.find((item) => {
    return item.id == id;
  });

  return (
    <div className="container">
      <BOX>
        {/* <TITLE color={"red"}>Details Page</TITLE> */}
        <TITLE className="red">Details Page</TITLE>
        {myAlert === true ? <Alert></Alert> : null}
      </BOX>
      <div className="row">
        <div className="col-md-6">
          <img className="product-img" src={selectedItem.img} alt="item" />
        </div>
        <div className="col-md-6 mt-4 text-left">
          <h4 className="pt-5">{selectedItem.title}</h4>
          <p>{selectedItem.content}</p>
          <p>${selectedItem.price}</p>
          <StockInfo
            stocks={props.stocks}
            selectedItem={selectedItem}
          ></StockInfo>
          <button
            className="btn btn-danger"
            onClick={() => {
              const getstocks = [...props.stocks];
              getstocks[selectedItem.id] = getstocks[selectedItem.id] - 1;
              props.setstocks(getstocks);
            }}
          >
            BUY
          </button>
          <button
            className="btn btn-primary ml-1"
            onClick={() => {
              history.goBack();
              // history.push("/custom-path");
            }}
          >
            Go Back
          </button>
        </div>
      </div>
    </div>
  );
}

function StockInfo(props) {
  return <p>Stocks: {props.stocks[props.selectedItem.id]}</p>;
}

function Alert() {
  return (
    <div className="my-alert-red">
      <p>Don't miss your chance!!</p>
    </div>
  );
}

